import React, { Component } from "react";
import API from "../../utils/API";
import Icon from "../../components/Icon";
import PB from "../../components/PB";
import Options from "../../components/Options";
import Fighter from "../../components/Fighter";
import FighterTop from "../../components/FighterTop";
import GameSelect from "../../components/GameSelect";
import "./controls.css";

class Controls extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: this.props.user,
            defSplits: {
                mtpo:   [{ category: "Any%", id: "5ad93dbaa79643ce5b21062b"},{category: "Highscore", id: "5ad93dbaa79643ce5b21062c"}],
                spo:    [{ category: "Any%", id: "5ad93dbaa79643ce5b21062a"}],
                powii:  [{ category: "Any%", id: "5acf8eb59872806d554c3ed3"},{category: "Contender%", id: "5acfbd3fd3d18c7049a9a7b7"}],
            },  
            newSplits: false, 
            runSplits: false,
            pbSplits: false, 
            games: [], 
            gameTitle: "",
            gameCategory: "",
            readyCheck: false,
            runStarted: false,
            runComplete: false,
            runRound: 0,
            img: "", 
            expand: {height: 0},
            expandState: false,
            topVis: {opacity: 1}
        }
    }

    componentDidMount() { this.getGames() };

// ------------------------------------------- getGames ----------------------------------------------------
//grabs game data from server

    getGames = () => {
        API.getGames()
            .then(res => { this.setState({games: res.data.results,})}
        ).catch(err => console.log(err));
    };

// ----------------------------------------- updatetUser ---------------------------------------------------
//grabs data from server based on user ID

    // updateUser = (id, info) => {
    //     API.updateUser(id, info)
    //         .then(res => { 
    //             //code to update socket.io
    //         }
    //     ).catch(err => console.log(err));
    // }

// ------------------------------------------ gameSelect----------------------------------------------------
//Sets the state to what game was selected

    gameSelect = event => {
        const { value, title, id} = event.target;
        this.setState({gameTitle: [value, id, title] })
    };

// ------------------------------------------- catSelect ---------------------------------------------------
//Sets teh state to what Category was selected

    catSelect = event => {
        const { value, name} = event.target;
        this.setState({gameCategory: value })
    };

// ------------------------------------------- readyCheck --------------------------------------------------
//

    readyCheck = (btn) => { 
        switch (btn) {
            case "play":
                if(this.state.gameTitle && this.state.gameCategory && !this.state.runStarted) 
                return false; else return true;
                break;
            case "prev":
                if(this.state.runStarted && this.state.runRound > 0) 
                return false; else return true;
                break;
            case "next":
                if(this.state.runStarted && this.state.runRound < this.state.runSplits.length -1) 
                return false; else return true;
                break;
            case "expand":
                if(this.state.runStarted) 
                return false; else return true;
                break;
            case "save":
                if(this.state.runStarted && this.state.runCompleted) 
                return false; else return true;
                break;
            case "reset":
                if(this.state.runStarted) 
                return false; else return true;
                break;
        }
    };


// -------------------------------------------- startRun ---------------------------------------------------
//starts the run
    startRun = () => {

        const game = this.state.games[this.state.gameTitle[1]].abv // game title abv....
        let split = this.state.user.splits[game].filter(param => param.category === this.state.gameCategory)
        let splitId

        this.setState({topVis: { opacity: 0}})

        setTimeout(function(){

        if(split.length) splitId = split[0]._id
        else {
            split = this.state.defSplits[this.state.gameTitle[2]].filter(param => param.category === this.state.gameCategory)
            splitId = split[0].id
            this.setState({newSplits: true})
        }

        API.getSplit(splitId)
            .then(res => { 
                this.setState({runSplits: res.data.results.splits, runStarted: true, topVis: {opacity: 1}})
                this.setState({img: this.state.runSplits[0].fighter.img})
                
               
    
                
            }
        ).catch(err => console.log(err));

    }.bind(this), 2000)
    };

// --------------------------------------------- expand ----------------------------------------------------
    expand = () => {
        let size = (32.5 * this.state.runSplits.length + 1) + "px"
        
        if(this.state.expandState) this.setState({expand: {height: 0}, expandState: false})
        else this.setState({expandState: true, expand: {height: size}})
    }

// ------------------------------------------- inputChange -------------------------------------------------
   
    inputChange = event => {
        const {value, name} = event.target;
        let splits = Object.assign([], this.state.runSplits); 
        
        splits[this.state.runRound].pb[name] = value;
        this.setState({runSplits: splits});
    };

// ----------------------------------------------- next ----------------------------------------------------
   
    next = event => { 
        this.setState({runRound: (this.state.runRound + 1) });
    };
// ------------------------------------------ slideChange --------------------------------------------------
//controls the sliders in options with onChange then updates database with change

    // sliderChange = event => { 
    //     const {checked, name} = event.target; //get name and the checked value
    //     let user = Object.assign({}, this.state.user);  //copy state user obj
    
    //     user.options[name] = checked;  //add the new info
                            
    //     this.setState({user}) //set the state with the new obj  
    //     this.updateUser(this.state.user._id, this.state.user) //update the server with new info
    // };

// --------------------------------------------- logOut ----------------------------------------------------
//redirects to logout page....

    // logOut = () => {window.location = "/logout"};
    
// ------------------------------------------ Frontend Code ------------------------------------------------

    render() {
        return (
            <div className="card mb-3">
                <div className="card-body">
                    <div className="row">
                        <div className="col-12">
                            <div className="row">
                                <div className="col-12 text-center">
                                    <h4> <Icon id="fas fa-gamepad" /> Controller </h4>
                                </div>
                            </div>
                        </div>
                        <div className="col-12 text-center">
                            <hr />
                            <div className="row">
                                <div className="col-12">      
                                    <div className="col-12 controlContainer">
                                        <div className="col=12" id="controlTop" >
                                            <div id="test" style={this.state.topVis}>
                                             { this.state.runStarted
                                               ? <FighterTop 
                                                    splits={this.state.runSplits[this.state.runRound]}
                                                    inputChange={this.inputChange}
                                               />
                                               : <GameSelect 
                                                    games={this.state.games} 
                                                    gameSelect={this.gameSelect}
                                                    catSelect={this.catSelect}
                                                    gameTitle={this.state.gameTitle}
                                                />

                                             
                                             }
                                            </div>
                                        </div>
                                        <div className="col-12 text-center" id="btnContainer">
                                            <button type="button" className="btn btn-sm btn-secondary controlBtn" disabled={this.readyCheck("reset")}   name="bestInfo"><Icon id="fas fa-undo-alt" /> Reset </button>
                                            <button type="button" className="btn btn-sm btn-secondary controlBtn" disabled={this.readyCheck("expand")} onClick={this.expand}    name="bestInfo"><Icon id="fas fa-expand" /> </button>
                                            <button type="button" className="btn btn-sm btn-secondary controlBtn" disabled={this.readyCheck("prev")}    name="bestInfo"><Icon id="fas fa-step-backward" /> </button>
                                            <button type="button" className="btn btn-sm btn-secondary controlBtn" disabled={this.readyCheck("play")}   onClick={this.startRun}  name="bestInfo"><Icon id="fas fa-play" /> Start </button>
                                            <button type="button" className="btn btn-sm btn-secondary controlBtn" disabled={this.readyCheck("next")}   onClick={this.next}      name="bestInfo"><Icon id="fas fa-step-forward" />  </button>
                                            <button type="button" className="btn btn-sm btn-secondary controlBtn" disabled={this.readyCheck("save")}    name="bestInfo"><Icon id="fas fa-save" /> Save </button>
                                        </div>
                                        
                                        
                                        <div className="col-12" id="expanded" style={this.state.expand}>
                                            <div className="row fighterRow key">
                                                <div className="col-1" id="">
                                                   
                                                </div>
                                                <div className="col-5 blah text-left fighterName" id="">
                                                    Fighter Name
                                                </div>
                                                <div className="col-2 blah text-left fighterName" id="">
                                                    KD1
                                                </div>
                                                <div className="col-2 blah text-left fighterName" id="">
                                                    KD2
                                                </div>
                                                <div className="col-2 newTime" id="">
                                                    <div className="form-group blah text-left">
                                                        Time
                                                    </div>
                                                </div>
                                            </div>

                                            { this.state.runSplits
                                                ?   this.state.runSplits.map((item, i) => (       
                                                        <Fighter 
                                                            key={item.fighter._id + i} 
                                                            splits={this.state.runSplits[i]}
                                                            i={i} 
                                                            inputChange={this.inputChange}     
                                                        />
                                                    ))
                                                :    console.log()
                                            }
                                        </div>
                                    </div>
                                </div>  
                            </div>
                            <hr />
                        </div>     
                    </div>
                </div>
            </div>
        )
    }
}

export default Controls;


