import React from "react";
import "./fighter.css";

const gray = (number) => {
    if(number % 2) return "row fighterRow dark"
    else return "row fighterRow"
}

const Fighter = props => 
   
<div className={gray(props.i)}>
    <div className="col-1">
        <img className="fighterIcon" src={require("./" + props.splits.fighter.icon)} alt="Card cap" /> 
    </div>
    <div className="col-5 text-left fighterName" id="">
        {props.splits.fighter.fighter}
    </div>
    <div className="col-2 newTime" id="">
        <div className="form-group blah">
            <input 
                type="text" 
                className="form-control newTimeInput"
                name="KD1"
                value={props.splits.pb.KD1} 
                onChange={props.inputChange}
            />
        </div>
    </div>
    <div className="col-2 newTime" id="">
        <div className="form-group blah">
            <input 
                type="text" 
                className="form-control newTimeInput" 
                name="KD2"
                value={props.splits.pb.KD2}
                onChange={props.inputChange} 
            />
        </div>
    </div>
    <div className="col-2 newTime" id="">
        <div className="form-group blah">
            <input 
                type="text" 
                className="form-control newTimeInput" 
                name={"splits." + props.i + ".pb.time"} 
                value={props.splits.pb.time}
                onChange={props.inputChange}
            />
        </div>
    </div>
</div>

export default Fighter;


